import React, { useState } from 'react';
import Editor from '@monaco-editor/react';
import { XMLParser } from 'fast-xml-parser';
import { useTable, useSortBy } from 'react-table';

const FlexiPageAnalyzer = () => {
  const [xmlContent, setXmlContent] = useState('');
  const [jsonContent, setJsonContent] = useState('');
  const [componentsData, setComponentsData] = useState([]);

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    const reader = new FileReader();

    reader.onload = (e) => {
      setXmlContent(e.target.result);
    };

    reader.readAsText(file);
  };

  const handleAnalyze = () => {
    if (!xmlContent) {
      alert('Please upload a FlexiPage XML file.');
      return;
    }

    try {
      const parser = new XMLParser({
        ignoreAttributes: false,
        attributeNamePrefix: "",
        textNodeName: "value",
      });
      const jsonObj = parser.parse(xmlContent);
      const components = parseFlexiPage(jsonObj);
      setJsonContent(JSON.stringify(components, null, 2));
      setComponentsData(generateComponentCount(components));
    } catch (error) {
      alert('Error parsing XML. Please make sure it is valid.');
    }
  };

  const parseFlexiPage = (json) => {
    const components = [];

    const parseComponentInstance = (component) => {
      const componentName = component.componentName;
      const visibilityRules = component.visibilityRule || [];
      const parsedRules = Array.isArray(visibilityRules) ? visibilityRules : [visibilityRules];

      return {
        componentName,
        visibilityRules: parsedRules.map((rule) => ({
          booleanFilter: rule.booleanFilter || null,
          criteria: (rule.criteria && Array.isArray(rule.criteria) ? rule.criteria : [rule.criteria] || []).map((c) => ({
            leftValue: c.leftValue,
            operator: c.operator,
            rightValue: c.rightValue,
          })),
        })),
      };
    };

    const regions = json.FlexiPage.flexiPageRegions;
    const regionList = Array.isArray(regions) ? regions : [regions];

    regionList.forEach((region) => {
      const itemInstances = region.itemInstances;
      const items = Array.isArray(itemInstances) ? itemInstances : [itemInstances];

      items.forEach((item) => {
        const componentInstances = item.componentInstance;
        const componentsList = Array.isArray(componentInstances) ? componentInstances : [componentInstances];

        componentsList.forEach((component) => {
          components.push(parseComponentInstance(component));
        });
      });
    });

    return components;
  };

  const generateComponentCount = (components) => {
    const componentCount = {};

    components.forEach((component) => {
      const { componentName } = component;
      if (componentCount[componentName]) {
        componentCount[componentName]++;
      } else {
        componentCount[componentName] = 1;
      }
    });

    return Object.entries(componentCount).map(([name, count]) => ({ name, count }));
  };

  const totalComponentCount = componentsData.reduce((total, { count }) => total + count, 0);

  const columns = React.useMemo(
    () => [
      {
        Header: 'Component Name',
        accessor: 'name',
        sortType: 'alphanumeric',
      },
      {
        Header: 'Count',
        accessor: 'count',
        sortType: 'basic',
      },
    ],
    []
  );

  const {
    getTableProps,
    getTableBodyProps,
    headerGroups,
    rows,
    prepareRow,
  } = useTable({ columns, data: componentsData }, useSortBy);

  return (
    <div className="container mx-auto my-4">
      <div className="flex flex-col items-center mb-4">
        <input type="file" accept=".xml" onChange={handleFileUpload} className="mb-2" />
        <button
          className="bg-blue-500 hover:bg-blue-700 text-white font-bold py-2 px-4 rounded"
          onClick={handleAnalyze}
        >
          Analyze
        </button>
      </div>
      <div className="grid grid-cols-2 gap-4 mb-4">
        <div>
          <h2 className="text-xl mb-2">FlexiPage XML</h2>
          <Editor
            height="40vh"
            language="xml"
            value={xmlContent}
            onChange={setXmlContent}
            theme="vs-dark"
          />
        </div>
        <div>
          <h2 className="text-xl mb-2">Analyzed JSON</h2>
          <Editor
            height="40vh"
            language="json"
            value={jsonContent}
            theme="vs-dark"
            options={{ readOnly: true }}
          />
        </div>
      </div>
      <div>
        <h2 className="text-xl mb-2">Component Count</h2>
        <table {...getTableProps()} className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            {headerGroups.map(headerGroup => (
              <tr {...headerGroup.getHeaderGroupProps()}>
                {headerGroup.headers.map(column => (
                  <th
                    {...column.getHeaderProps(column.getSortByToggleProps())}
                    className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider"
                  >
                    {column.render('Header')}
                    <span>
                      {column.isSorted ? (column.isSortedDesc ? ' 🔽' : ' 🔼') : ''}
                    </span>
                  </th>
                ))}
              </tr>
            ))}
          </thead>
          <tbody {...getTableBodyProps()} className="bg-white divide-y divide-gray-200">
            {rows.map(row => {
              prepareRow(row);
              return (
                <tr {...row.getRowProps()}>
                  {row.cells.map(cell => (
                    <td
                      {...cell.getCellProps()}
                      className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900"
                    >
                      {cell.render('Cell')}
                    </td>
                  ))}
                </tr>
              );
            })}
          </tbody>
          <tfoot>
            <tr>
              <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">TOTAL:</td>
              <td className="px-6 py-4 whitespace-nowrap text-sm font-medium text-gray-900">{totalComponentCount}</td>
            </tr>
          </tfoot>
        </table>
      </div>
    </div>
  );
};

export default FlexiPageAnalyzer;

